<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <yd-infinitescroll :callback="loadList" ref="infinitescrollDemo">
        <div class="tadelist" theme="1" slot="list">
          <div class="tade_item" v-for="item in list"  @touchstart="showDeleteButton(item)" @touchend="clearLoop">
            <yd-flexbox class="trade_info">
                <yd-flexbox-item class="trade_addr">
                  <span class="left_d">{{item.Remark}}</span>
                </yd-flexbox-item>
                <div><span class="dian"></span></div>
            </yd-flexbox>
            <yd-flexbox class="trade_dabao">
                <div><span class="zwf"></span></div>
                <yd-flexbox-item class="succorfail">
                  <span>Address：{{item.Target | ordernum}}</span>
                  <span @click="deladdress(item)">
                    <img src="../assets/images/del.png" style="width:.4rem">
                  </span>
                </yd-flexbox-item>
            </yd-flexbox>
          </div>
        </div>
        <span slot="doneTip" v-show="list==[]">{{$t('toast.nodata')}}</span>
        </yd-infinitescroll>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
  import {getCookie,setCookie} from '../assets/js/cookie.js'
export default {
  beforeCreate () {
    var url = this.$route.path
    setTimeout(function(){
      document.querySelector('body').setAttribute('style', 'background:#f4f4f4')
    },10)
  },
  beforeDestroy () {
    document.querySelector('body').setAttribute('style', 'background:#ffffff')
  },
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('mycenter.telbook'),
         right:true,
         bj:'#2c94ed',
         addbook:true
      },
      page:1,
      pagesize:10,
      pagenum:0,
      list:[],
      loadtext:'加载更多',
      lasttime:'',
      systemcontent:''
    }
  },
  methods:{
    clearLoop() {
        clearInterval(this.Loop);
    },
    showDeleteButton(item) {
      var that = this
        clearInterval(this.Loop);//再次清空定时器，防止重复注册定时器
        this.Loop=setTimeout(function(){
            this.deladdress(item)
        }.bind(this),1000);
    },
    deladdress(item){
      var that = this
      this.$dialog.confirm({
          //title: '删除操作',
          mes: that.$t('data.isdelete')+'[ '+item.Remark+' ]',
          opts: () => {
            this.axios({
              url:this.Url + 'AddressBook.ashx',
              method:'post',
              params: {req:3,id:item.Id}
            }).then(res=>{
              that.$dialog.toast({mes: res.data.msg, timeout: 1000})
              setTimeout(function(){
                that.list=[]
                that.page=1
                that.loadList()
              },1000)
            })
          }
      });
    },
    gotradetail(item) {
      if (item.MessageType == 2) {
        this.$router.push({path:'/index_order_detail',query:{id:item.MessageDetail}})
      }else{
        this.systemcontent = item
      }
    },
    loadList() {
      this.openloading(this.$t('toast.loding'))
      var that = this;
      this.axios({
        url:this.Url + 'AddressBook.ashx',
        method:'post',
        params: {req:4,collector:getCookie("publickey"),pageindex:this.page,pagecount:this.pagesize}
      }).then(function (res) {
        that.closeloading()
          var _list = res.data.data;
          if (res.data.data.length == 0) {
            that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone');
              that.$dialog.toast({
                  mes: that.$t('toast.nodata'),
                  timeout: 1500
              });
              return;
          }else{
            that.list = [...that.list, ..._list];
            that.pagenum = res.data.totalCount
            if (res.data.data.length == 0) {
                /* 所有数据加载完毕 */
                that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone');
                return;
            }
            that.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.finishLoad');
            that.page++;
          }
      }).catch(function(res){
          that.closeloading()
          res = JSON.stringify(res)
          that.$refs.pullrefreshDemo.$emit('ydui.pullrefresh.finishLoad');
          if(JSON.stringify(JSON.parse(res).request)=="{}"){
              that.catchErr()
              setTimeout(function(){
                  that.$dialog.confirm({
                      title: that.$t('data.networkerr')+'...',
                      opts: (res) => {
                          that.loadList(2) 
                      }
                  });
              },2000)
          }
      });
    }
  },
  created(){
    this.loadList()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.loaddata{
  width: 100%;
  text-align: center;
  background: #fff;
  height: 45px;
  line-height: 45px;
  color: #888;
  font-size: .22rem;
  border-top: 1px solid #f1f1f1
}
.red{
  background: red;
}
.tade_item{
  background: #fff;
  margin-top: .35rem;
}
.yd-progressbar>svg{
  position: absolute!important;
}
.trade_time{
  height: .8rem;
  line-height: .8rem;
  padding-left: .3rem;
  font-size: .28rem;
  background: #f4f4f4;
}
.trade_info{
  padding: .35rem .3rem .1rem .3rem;
}
.dian,.zwf{
  width: .12rem;
  height: .12rem;
  border-radius: .12rem;
  display: block;
} 
.dianblue{
  background: #278eed
}
.diancolorblue{
  color: #278eed
}
.diancoloryellow{
  color: #ed6d27
}
.dianyellow{
  background: #ed6d27
}
.trade_addr .left_d{
  float: left;
  font-size: .3rem;
  color: #333;
  margin-left: .15rem;
}
.trade_addr p{
  float: right;
  font-size: .32rem;
  text-align: right;
}
.trade_dabao{
  padding: .1rem .3rem .35rem .3rem
}
.succorfail span:nth-child(1){
  font-size: .25rem;
  color: #666;
  display: block;
  float: left;
}
.succorfail span:nth-child(2){
  font-size: .25rem;
  color: #999;
  display: block;
  float: right;
  margin-top: -3px;
}
.yd-confirm-hd{
  text-align: center!important;
}
</style>



